module.exports = function(router){
	router.map({
		'/':{
			name:'home',
			component: require('./views/home.vue')
		},
		// not found handler
	    '*': {
	      component: require('./views/not_found.vue')
	    },
		'/product_detail_view/:carId': {
			name:'product_detail_view',
			component: require('./views/product_detail.vue')
		},
		'/my_buy_car/:carId': {
			name:'my_buy_car_view',
			component: require('./views/my_buy_car.vue')
		},
		'/book1/:carId': {
			name:'book1_view',
			component: require('./views/book1.vue')
		},
		'/book2/:carId': {
			name:'book2_view',
			component: require('./views/book2.vue')
		},
		'/book3/:carId': {
			name:'book3_view',
			component: require('./views/book3.vue')
		},
		'/book4/:carId': {
			name:'book4_view',
			component: require('./views/book4.vue')
		},
		'/checkout/:shoppingCarId': {
			name:'checkout_view',
			component: require('./views/checkout.vue')
		},
		'/pay_success/':{
			name:'pay_success_view',
			component: require('./views/pay_success.vue')
		}
	});

	// router.beforeEach((transition) => {
	// if (transition.to.path === '/forbidden') {
	// 	router.app.authenticating = true
	// 	setTimeout(() => {
	//     router.app.authenticating = false
	//     alert('this route is forbidden by a global before hook')
	//     transition.abort()
	//   }, 1500)
	// } else {
	//   transition.next()
	// }
	// })

	router.beforeEach(function(transition){
		//如果是中止，这里可以判断用户登录
		console.log(transition.to.path);
		if(transition.to.path!=='/'){
			router.app.isIndex = false;
		}
		if(transition.to.path === '/forbidden'){
			router.app.authenticating = true
			setTimeout(function(){
				router.app.authenticating = false
				alert('此路由在全局中设置为中止');
				transition.abort();
			},1500);
		} else {
			 transition.next();
			 console.log('每次路由开始的时候beforeEach');
		}
	});

	router.afterEach(function(transition){
		setTimeout(function(){
			window.scrollTo(0,0);
		},180);
	});

}


